import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { interval, Observable } from 'rxjs';
import { CloudService } from 'src/app/cloud.service'; 

@Component({
  selector: 'app-movies',
  templateUrl: './movies.component.html',
  styleUrls: ['./movies.component.css']
})
export class MoviesComponent implements OnInit {

  name:string;
  movie: [];
  moviess$:Observable<any>;


  sub: string = 'dssdsfdsfdsfdsfdstretwe';


  constructor(private cloud: CloudService, private route: ActivatedRoute) { }

  ngOnInit(): void {

 
    this.route.params.subscribe(res=>{

      console.log('movie',res)
      this.onGetMoviesData(res);

    })
    
    this.route.params.subscribe((res:any)=>{
      this.name = res.name;
    })

 
    

  }

  onGetMoviesData(data){
    this.cloud.getMoviesList(data).subscribe((res:any)=>{
      this.movie = res.results;
    })
    // this.moviess$ = this.cloud.getMoviesList(data);
  }



}
