import { ThrowStmt } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { CloudService } from 'src/app/cloud.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  
  popularMovie: [];
  topRated:[];
  upCom:[];

  show = false;



 


  constructor(private cloud: CloudService) { }

  ngOnInit(): void {


    this.cloud.getPop().subscribe((res:any)=>{
            
      this.popularMovie = res.results;
      (error)=>{
        alert('Not Found')
        console.log('error Here')
      }
      this.show = true;
    })

    this.cloud.getTopRated().subscribe((res:any)=>{
      this.topRated = res.results;
      this.show = true
    })

    this.cloud.getUpCom().subscribe((res: any)=>{
      this.upCom = res.results;
      this.show = true
    
    })

  }

}
