import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CloudService } from 'src/app/cloud.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private fb: FormBuilder, private router: Router, private cd: CloudService ) { }

  login:any;
  name: string;


 

  ngOnInit(): void {

    this.login = this.fb.group({
      name: ['',[Validators.required]],
      password: ['', [Validators.required]]
    
    })


  }

  get f(){
    return this.login.controls
  }

  logIn(){
    if(this.login.invalid){
      return 
    }

    alert ('You Are online')
    this.cd.login = true;
    localStorage.setItem('name', this.login.value.name)
    localStorage.setItem('password', this.login.value.password)
    this.router.navigate(['/disney'])
    
  console.log(this.login.value.name , this.login.value.password)

  }

}
