import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'image'
})
export class ImagePipe implements PipeTransform {

  transform(value: string, change: string = 'assets/noimage.jpg'):any{
    
    if(value === '' || value === undefined || value === null){
      value = change

    }
   return value
    }

}
