import { Route } from '@angular/compiler/src/core';
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Routes } from '@angular/router';
import { CloudService } from 'src/app/cloud.service';

@Component({
  selector: 'app-disney',
  templateUrl: './disney.component.html',
  styleUrls: ['./disney.component.css']
})
export class DisneyComponent implements OnInit {

  constructor(private cd:CloudService, private router: ActivatedRoute) { }

  movieData: any;
  name: string;

  ngOnInit(): void {
    this.cd.getTopRated().subscribe((res:any)=>{
      this.movieData = res.results;
    })

    this.name =  this.router.snapshot.params['disney'];

  }

}
