import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { CloudService } from 'src/app/cloud.service';

@Component({
  selector: 'app-movie-details',
  templateUrl: './movie-details.component.html',
  styleUrls: ['./movie-details.component.css']
})
export class MovieDetailsComponent implements OnInit {

  movieDetail:[];
  name:string;
  id:any;
  similar:[];
  caste:any;


  constructor(private route: ActivatedRoute, private cloud: CloudService) { }

  ngOnInit(): void {

    this.id = this.route.params.subscribe(res=>{
      console.log('details',res)
      this.onGetMoviesDetails(res);

    })

    this.id = this.route.params.subscribe(res=>{
      console.log('similar',res)
      this.onGetSimilarMovies(res);
    })

    this.id = this.route.params.subscribe(res=>{
      console.log('caste', res)
      this.onGetMoviesCaste(res);
    })

  }

  onGetMoviesDetails(data){
    this.cloud.getMovieDetails(data).subscribe((res:any)=>{
      this.movieDetail = res;
    })
  }

  onGetSimilarMovies(data){
    this.cloud.getSimilarMovies(data).subscribe((res: any)=>{
      this.similar = res.results;
    })
  }

  onGetMoviesCaste(data){
    this.cloud.getMoviesCaste(data).subscribe((res:any)=>{
    this.caste = res.cast;
    })
  }

}
