import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { ActorComponent } from './components/actor/actor.component';
import { HeaderComponent } from './components/header/header.component';
import { FooterComponent } from './components/footer/footer.component';
import { ChannelsComponent } from './components/channels/channels.component';
import { HomeComponent } from './components/home/home.component';
import { MovieDetailsComponent } from './components/movie-details/movie-details.component';
import { MoviesComponent } from './components/movies/movies.component';
import { SportsComponent } from './components/sports/sports.component';
import { RouterModule,Routes} from '@angular/router';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { CanActivateGuard } from './can-activate.guard';
import { CanDeactivateGuard} from './can-deactivate.guard';

import {MatProgressBarModule} from '@angular/material/progress-bar'; 
import {MatButtonModule} from '@angular/material/button'; 
import {MatToolbarModule} from '@angular/material/toolbar';  
import {MatInputModule} from '@angular/material/input'; 
import {MatMenuModule} from '@angular/material/menu'; 
import {MatIconModule} from '@angular/material/icon';
import {MatBottomSheetModule} from '@angular/material/bottom-sheet';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import {MatProgressSpinnerModule} from '@angular/material/progress-spinner';


import { AlertModule } from 'ngx-bootstrap/alert';

import { LoadingBarRouterModule } from '@ngx-loading-bar/router';
import { NgxSkeletonLoaderModule } from 'ngx-skeleton-loader';

import { CarouselModule } from 'ngx-bootstrap/carousel';
import { ImagePipe } from './image.pipe';
import { LoginComponent } from './components/login/login.component';
import { DisneyComponent } from './components/disney/disney.component';



const routes:Routes = [
  {path: 'channels/:id/:name', component: ChannelsComponent},
  {path: 'movies/:id/:name', component: MoviesComponent},
  {path: 'sports/:id/:name', component: SportsComponent},
  {path: 'in/:type/:name/:id', component: MovieDetailsComponent},
  {path: 'actor/:id', component: ActorComponent},
  {path: 'in', component: HomeComponent},
  {path: 'login', component: LoginComponent},
  {path: 'disney', component: DisneyComponent, canActivate:[CanActivateGuard], canDeactivate: [CanDeactivateGuard]},
  {path: '', component: HomeComponent},
  {path: '**', component: HomeComponent},
]


@NgModule({
  declarations: [
    AppComponent,
    ActorComponent,
    HeaderComponent,
    FooterComponent,
    ChannelsComponent,
    HomeComponent,
    MovieDetailsComponent,
    MoviesComponent,
    SportsComponent,
    ImagePipe,
    LoginComponent,
    DisneyComponent
  ],
  imports: [
    BrowserModule,
 
    AppRoutingModule,
    MatButtonModule,
    MatToolbarModule,
    MatInputModule,
    MatMenuModule,
    BrowserAnimationsModule,
    MatIconModule,
    RouterModule.forRoot(routes),
    HttpClientModule,
    MatBottomSheetModule,
    NgbModule,
    AlertModule,
    FormsModule, ReactiveFormsModule,
    CarouselModule,
    MatProgressBarModule,MatProgressSpinnerModule,
    LoadingBarRouterModule,
    NgxSkeletonLoaderModule.forRoot({ animation: 'pulse', loadingText: 'This item is actually loading...' })
  ],
  providers: [ CanActivateGuard, CanDeactivateGuard],
  bootstrap: [AppComponent]  
})
export class AppModule { }
