import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CloudService } from 'src/app/cloud.service';

@Component({
  selector: 'app-channels',
  templateUrl: './channels.component.html',
  styleUrls: ['./channels.component.css']
})
export class ChannelsComponent implements OnInit {

  prod: string;
  movie: [];
  name: string;
  progress: boolean = false;


  constructor(private route: ActivatedRoute, private cloud: CloudService) {
   }

  ngOnInit(): void {

    this.route.params.subscribe(res=>{
   
      console.log('result',res);
      this.onGetMoviesData(res);
    
    })
    // this.name = this.route.snapshot.params['name'];

    this.route.paramMap.subscribe((res:any)=>{
      this.name = res.params.name
      console.log('paramsMap',this.name)
    })

    // this.route.params.subscribe((res:any)=>{
    //   this.name = res.name
    //   console.log(res)
    // })
    

  }

  onGetMoviesData(name){
    this.cloud.getMoviesList(name).subscribe((res: any)=>{
      this.movie  = res.results;
    })

}
}
