import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';
import { CloudService } from 'src/app/cloud.service';

@Component({
  selector: 'app-sports',
  templateUrl: './sports.component.html',
  styleUrls: ['./sports.component.css']
})
export class SportsComponent implements OnInit {

  name:string;
  movies2:Observable<any>;
  movies: [];


  constructor(private route: ActivatedRoute, private cloud: CloudService) { }

  ngOnInit(): void {

    this.route.params.subscribe(res=>{
      this.onGetMoviesData(res);
    })
    
    this.route.paramMap.subscribe((res:any)=>{
      this.name = res.params.name;
    })
    


  }

  onGetMoviesData(data){
    this.cloud.getMoviesList(data).subscribe((res:any)=>{
      this.movies2 = res.results;
    })

    // this.movies2 = this.cloud.getMoviesList(data);
  }

}
